body {
  --font-primary: Hind Madurai, sans-serif;
  --font-secondary: Barlow Condensed, sans-serif;
  --font-size: 18px;
  --line-height: 1.77;
  --font-extralight: 200;
  --font-light: 300;
  --font-regular: 400;
  --font-semibold: 600;
  --font-bold: 700;
  --font-extrabold: 800;
  --font-black: 900;
  --btn-radius: 20px;
  --card-radius: 30px;
  --bullet-size: 12px;
}

body {
  --background: #ffffff;
  --background-negative: #16255b;
  --background-primary: #dc1216;
  --background-secondary: #b9bdcd;
  --background-tertiary: #f3f4f6;
  --foreground: #16255b;
  --foreground-negative: #ffffff;
  --foreground-primary: #dc1216;
  --foreground-secondary: #16255b;
  --foreground-tertiary: #16255b;
