button,
.btn {
  display: inline-block;
  padding: 0;
  border: none;
  outline: none;
  background: none;
  -webkit-appearance: none;
          appearance: none;
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  transition: ease-in-out 350ms;
  transition-property: background-color, color, border, opacity;
